<template>
<div>
    <input ref="input" value="This is a piece of text">
    <u-button @click="onClick">Select</u-button>
</div>
</template>
<script>
import { utils } from 'library';

export default {
    data() {
        return {
            selectedText: '',
        };
    },
    methods: {
        onClick() {
            this.selectedText = utils.select(this.$refs.input);
        },
    },
};
</script>
