<template>
<div>
    <div ref="container">A piece of text</div>
    <u-button @click="onClick">Select</u-button>
</div>
</template>
<script>
import { utils } from 'library';

export default {
    data() {
        return {
            selectedText: '',
        };
    },
    methods: {
        onClick() {
            this.selectedText = utils.select(this.$refs.container);
        },
    },
};
</script>
